.main{
	position: relative;
	height: 100%;
	width: 100%;
    /* border-top: 300px solid rgba(86, 26, 96, 0.807); */
}
.main h1{
	position: relative;
	height: auto;
	width: 550px;
	left: 50%;
	transform: translateX(-50%);
	margin:5% 0 2% 0;
	text-align: center;
	font-size: 2.8em;
	font-weight: 650;
	letter-spacing: 1.5px;
	color:#000000;
	background:rgba(235, 74, 38, 0.227);
    font-family: 'Salsa' ,cursive;
}
.main p{
	position: relative;
	height: auto;
	width: 100%;
	text-align: center;
	font-weight: 500;
	font-size: 1.5em;
	letter-spacing: 1px;
	color: #777;
    font-family: 'Salsa' ,cursive;
}
.main .gallery{
	position: relative;
	height: 150vh;
    height: auto;
	width: 85%;
	margin: auto;
	padding: 45px 0;
	display: grid;
	grid-template-columns: auto auto auto auto; /*i write auto four time because we want four rows*/
	grid-gap: 2vh;
	grid-auto-flow: dense;
}
.main .gallery .img{
	position: relative;
	height: 100%;
	width: 100%;
	overflow: hidden;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.9 );
	border-radius: 2px;
   
}
.main .gallery .img:first-child{
	grid-column-start: span 2;
	grid-row-start: span 2.5;
    border-radius: 0px 60px  0px 60px;
}
.main .gallery .img:nth-child(3){
	/* grid-row-start: span 2; */
       border-radius:  0px 0px 60px 0px ;
}
.main .gallery .img:nth-child(2n+3){
	grid-row-start: span 1;
      
}
.main .gallery .img:nth-child(2){
	/* grid-row-start: span 2; */
    border-radius:  60px 0px 0px 0px ;
}
.main .gallery .img:nth-child(4){
	grid-row-start: span 2;
    border-radius:  60px 0px 0px 0px ;
}
.main .gallery .img:nth-child(5){
	grid-column-start: span 2;
	grid-row-start: span 2;
    border-radius:  0px 0px 60px 0px ;
}
.main .gallery .img:nth-child(4n+6){
	/* grid-column-start: span 2; */
	grid-row-start: span 2;
    border-radius:  0px 60px 0px 60px ;
}
.main .gallery .img:nth-child(6n+7){
	grid-row-start: span 1;
    border-radius:  60px 0px 0px 0px;
}
.main .gallery .img:nth-child(8){
	grid-column-start: span 1;
	grid-row-start: span 1;
    border-radius:  0px 0px 60px 0px ;
}
.main .gallery .img img{
	height: 100%;
	width: 100%;
	object-fit: cover;
	filter: brightness(0.6) grayscale(100);
	border-radius: 2px;
	transition: 0.3s ease-in-out;
}
.main .gallery .img:hover img{
	filter: brightness(1) grayscale(0);
    scale: 1.05;
}

@media only screen and (max-width: 768px){
	.main h1{
		font-size: 1.8em;
		width: 400px;
	}
	.main p{
		font-size: 1.2em;
	}
	.main .gallery{
		display: grid;
		grid-template-columns: auto auto auto;
	}
}
@media only screen and (max-width: 430px){
	.main h1{
		font-size: 1.2em;
		width: 300px;
	}
	.main p{
		font-size: 0.89em;
	}
	.main .gallery{
		display: block;
	}
	.main .gallery .img{
		display: block;
		width: 100%;
		height: 100%;
		margin:3% 0;
	}
	.main .gallery .img img{
		display: block;
		filter: brightness(1) grayscale(0);
        
	}
    .main .gallery .img:nth-child(n){
        border-radius: 8px;
    }
}




/* Highlight.css */

/* Add your existing styles here */

.loading-shimmer-container {
    display: inline-block;
    position: relative;
  }
  
  .loading-shimmer {
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
    background-size: 200% 100%;
    animation: shimmer 1.5s infinite linear;
    border-radius: 4px;
  }
  
  @keyframes shimmer {
    to {
      background-position: -200% 0;
    }
  }
  